import React, { useState } from "react";
import Sidebar from "../components/Sidebar";
import Navbar from "../components/Navbar";
import HeroSection from "../components/HeroSection";
import InfoSection from "../components/InfoSection";
import {
  homeObjOne,
  homeObjTwo,
  homeObjThree,
} from "../components/InfoSection/Data";
import Services from "../components/Services";
import MoreServicesSection from "../components/HeroSection/MoreServicesSection";
import OptionSection from "../components/HeroSection/OptionSection";
import InnobeeInfoSection from "../components/HeroSection/InnobeeInfoSection";

const Home = () => {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => {
    setIsOpen(!isOpen);
  };

  return (
    <>
      <Sidebar isOpen={isOpen} toggle={toggle} />
      <Navbar toggle={toggle} />
      <HeroSection />
      <InfoSection {...homeObjOne} />
      <InfoSection {...homeObjTwo} />
      <Services />
      {/* <InnobeeInfoSection /> */}
      <InfoSection {...homeObjThree} />
      {/* {/* <MoreServicesSection /> */}
      <OptionSection />
    </>
  );
};

export default Home;
