export const ContentBox = styled.div`
  width: 100%;
  padding-top: 0;
  padding-bottom: 10px;
  margin: 0;
  color: #fff;

  @media screen and (max-width: 768px) {
    padding: 100px 0;
  }
`;

export const ContentHeader = styled.h1`
  margin-bottom: 24px;
  font-size: 48px;
  line-height: 1.1;
  font-weight: 600;
  color: #f7f8fa;
  text-align: center;

  @media screen and (max-width: 480px) {
    font-size: 18px;
  }
`;

export const ContentHeaderParagraph = styled.p`
  margin-bottom: 35px;
  font-size: 18px;
  line-height: 24px;
  color: #fff;
  text-align: center;
`;

export const ContentHeader2 = styled.h2`
  margin-bottom: 18px;
  font-size: 36px;
  line-height: 0.8;
  font-weight: 600;
  color: #f7f8fa;
  text-align: center;

  @media screen and (max-width: 480px) {
    font-size: 16px;
  }
`;

export const ContentInformation = styled.div`
  display: flex;
  box-sizing: border-box;
  align-items: center;
`;

export const Wrapper = styled.div`
  padding: 10px;
  text-align: justify;
  width: 150px;
  display: block;
  margin-left: auto;
  margin-right: auto;

  @media screen and (max-width: 480px) {
    display: inline-block;
    width: 75px;
    padding: 5px;
  }
`;

export const ImageCover = styled.img`
  width: 150px;
  height: 150px;
  border-radius: 50%;
  margin: 20px;
  display: block;
  margin: 0 auto;

  object-fit: cover;
  object-position: center right;

  @media screen and (max-width: 480px) {
    width: 50%;
    display: inline-block;
    margin: 10px;
  }
`;
